.taskbarButton {
  cursor: pointer;
  padding: 0 4px;
  margin: 0 2px;
  border-radius: 3px;
  height: 24px;
  display: inline-flex;
  align-items: center;
  transition: all .1s ease-out;
  &:hover {
    // background-color: rgba(243, 244, 246, 0.3);
    color: var(--color-hover);
    background-color: var(--bgColor-hover);
  }
}
