.wallpaper {
  display: flex;
  height: 100vh;
  width: 100%;
  position: absolute;
  z-index: -1;
  &::after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    background-image: url(~@/assets/image/mask.png);
  }
  & > div {
    width: 100%;
  }
  .item {
    width: 100vw;
    height: 100vh;
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
  }
}
